@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&family=Roboto:wght@300&display=swap);
.app {
  font-family: Open Sans;
  
}

.app__body{
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100vw;
  background-color: #ededed;
  display: flex;
  margin-left: -10px;
}

body{
    font-family: Arial, Helvetica, sans-serif;
}

.sidebar{
    display: flex;
    flex: 0.30 1;
    flex-direction: column;
    
}

.sidebar__header{
    display: flex;
    padding: 10px 16px;  
    justify-content: space-between;
    border-right: 1px solid lightgray;
    height: 49px;
}

.sidebar__headerRight{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
    
}

.sidebar__headerRight > .MuiSvgIcon-root {
    margin-right: 2vw;
    font-size: 24px !important;
    align-self: center;

}

.sidebar__search{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
}

.sidebar__searchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    border-radius: 20px;
    width: 100%;
    height: 35px;
}

.sidebar__searchContainer > input{
    border: none;
    outline: 0;
    width: 100%;
}

.sidebar__searchContainer > .MuiSvgIcon-root {
    color: gray;
    padding: 10px;  
}

.sidebar__chats{
    flex: 1 1;
    background-color: white;
    overflow: scroll;
    scrollbar-width: thin;
}
.sidebarChat{
    display: flex;
    padding-left: 20px;
    padding-right: 20px;
    cursor: pointer;
    border-bottom: 1px solid #f6f6f6;
}

a{
    text-decoration: none;
    color: black;
}

.sidebarChat:hover{
    background-color: #ebebeb;
}

.sidebarChat__info > h2{
    font-size: 16px;
    margin-top: 0px;
    margin-bottom: 6px;
}
.sidebarChat__info{
    margin-left: 15px;
}
.sidebarChat__info > p{
    font-size: 12px;
    margin-bottom: 10px;
    margin-top: 5px;
    color: gray;
}


.chat{
    display: flex;
    flex: 0.70 1;
    flex-direction: column;
}

.chat__header{
    padding: 10px 16px;  
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray;
    height: 49px;
}

.chat__headerLeft{
    flex: 1 1;
    padding: 0px;
    padding-left: 20px;
}

.chat__headerLeft > h2{
    margin-bottom: 8px;
    font-size: 18px;
}

.chat__headerLeft > p{
    font-size: 14px;
    color: gray;
    margin-top: 0px;
}

.chat__headerRight{
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}

.chat__body{
    flex: 1 1;
    background-image: url(/WhatsAppClone/static/media/bg.bded4f44.png);
    background-repeat: repeat;
    background-position: center;
    overflow: scroll;
    padding: 30px;

}

.chat__message {
    position: relative;
    font-size: 13px;
    padding: 10px;
    padding-top: 20px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: white;
    margin-bottom: 5px;
    border-radius: 10px;
    min-width: -webkit-max-content;
    min-width: max-content;
}

.chat__message__chatname{
    position: absolute;
    top: 0;
    font-size: 14px;
    font-weight: bold;
    min-width: -webkit-max-content;
    min-width: max-content;
}

.chat__message__timestamp{
    font-size: xx-small;
    margin-left: 10px;
    flex: 1 1;
    right: 0;
}

.chat__receiver{
    margin-left: auto;
    background-color: #dcf8c6;
}

.chat__footer{
    display: flex;
    padding: 15px;
    justify-content: space-between;
    border-top: 1px solid lightgray;
  
}

.chat__footer > form{
    flex: 1 1;
    display: flex;

}

.chat__footer>form>input{
    margin-left: 10px;
    background-color: white;
    padding: 15px;
    border: 0;
    border-radius: 50px;
    outline: 0;
    font-size: 16px;
    flex: 1 1;
    margin-right: 15px;
}

.chat__footer > .MuiSvgIcon-root{
    color: gray;
    align-self: center;
    
}

.chat__footer > form > button{
    display: none;
}
body{
    overflow: hidden;
}
.login{
    display: grid;
    place-items: center;
    background-color: #3fc350;
    height: 110vh;
    width: 110vw;
    margin-left: -5vw;
    margin-top: -5vh;
}
.login__container{
    display: flex;
    flex-direction: column;
    text-align: center;
    background-color: rgb(182, 212, 211);
    border-radius: 10px;
    box-shadow: 2px 3px 5px gray;
    padding: 40px;
}
.login__container>button{
    background-color: #3fc350;
    font-size: larger;
    font-weight: bolder;
    border: 2px solid lightcoral;
    border-radius: 10px;
}

.login__container>h3{
    font-weight: 400;
}
.login__container>.img{
    background-image: url(/WhatsAppClone/static/media/logo.3c500401.svg);
    background-size: cover;
    margin: auto;
    width: 200px;
    height: 200px;
}
